<template>
  <div class="contentHtml">
	<ul class="iconType">
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/classroom/classroomType/0','免费课堂')" src="../../../assets/chushouwangdian@3x.png"/>
			<p>免费课堂</p>
		</li>
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/classroom/classroomType/1','付费课堂')" src="../../../assets/fabuqiugou@3x.png"/>
			<p>付费课堂</p>
		</li>
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/classroom/classroomType/2','热门课堂')" src="../../../assets/wangdianpinggu@3x.png"/>
			<p>热门课堂</p>
		</li>
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/classroom/classroomType/3','已报课堂')" src="../../../assets/yigouwangdian@3x.png"/>
			<p>已报课堂</p>
		</li>
	</ul>
	<div class="initBox mb10">
		<div class="initTitle"><span>热门课堂</span><span>更多></span></div>
		<div style="width:100%;overflow-x: scroll;">
			<ul class="hotClassroom">
				<template v-for="listData in hotListDatas">
					<li v-on:click="hrefTo('hrefTo','/index/classroom/classroomDetail/'+listData.id,'课堂详情')">
						<img v-bind:src="listData.lesson_head_img" width="150" height="100"/>
						<div class="oh"><span>{{listData.start_time | timeFormatter}}</span><span class="fr colorA">¥2323.23</span></div>
					</li>
				</template>
			</ul>
		</div>
	</div>
	<div class="initBox">
		<div class="initTitle"><span>电商课堂</span><span>更多></span></div>
		<div style="padding: 5px;border-bottom: 1px solid #eee;">
			<div style="padding:10px;"><img class="textImg" src="../../../assets/chushouwangdian@2x.png" width="30" height="30">  会飞的老虎</div>
			<img src="../../../assets/img.png" style="width: 100%;" />
			<div class="color9"><span>直播中</span><span style="margin-left:20px;">在线人数:12222</span><span class="fr">免费</span></div>
		</div>
		<div style="padding: 5px;border-bottom: 1px solid #eee;">
			<div style="padding:10px;"><img class="textImg" src="../../../assets/chushouwangdian@2x.png" width="30" height="30">  会飞的老虎</div>
			<img src="../../../assets/img.png" style="width: 100%;" />
			<div class="color9"><span>直播中</span><span style="margin-left:20px;">在线人数:12222</span><span class="fr">免费</span></div>
		</div>
		<div style="padding: 5px;border-bottom: 1px solid #eee;">
			<div style="padding:10px;"><img class="textImg" src="../../../assets/chushouwangdian@2x.png" width="30" height="30">  会飞的老虎</div>
			<img src="../../../assets/img.png" style="width: 100%;" />
			<div class="color9"><span>直播中</span><span style="margin-left:20px;">在线人数:12222</span><span class="fr">免费</span></div>
		</div>
	</div>
     <!--<div class="lodingDiv loding"></div>-->
  </div>
</template>

<script>
$("title").text("竞价");

export default {
  name: 'contentHtml',
  data () {
    return {
    	typeIndex: 0,
    	hotListDatas:[],
    	input:'',
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var per_page = 6;
    		var datas = {
	        "username":"",
			"type":1,
			"is_hot":-1,
			"end_time":"",
			"status":1,
			"title":"",
			"pageIndex":0,
			"pageSize":10,
		}
	  	$.get(_this.apiUrl+'/e/wxapi/lesson_list.php',datas,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		_this.hotListDatas = res;
	  		$(".hotClassroom").css("width",160*res.length+"px");
		},'json')
  	},
	hrefTo(type,val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	    }else{
	  		this.$router.push(val)
  		}
	},
	typeIndexs(index){
		this.typeIndex = index;
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.typebox{text-align: center;line-height:24px;padding: 20px 0; font-size: 16px;background: #fff;}
	.typebox span{width: 49%;display: inline-block;}
	.typebox span:first-child{border-right: 1px solid #CCCCCC;}
	.iconType{text-align: center;font-size:12px;background: #fff;margin-bottom:10px;overflow: hidden;}
	.iconType li{width: 25%;padding:15px 0;float: left;}
	.iconType li img{width:3rem;height: 3rem;}
	
	.initTitle{padding-bottom: 10px; border-bottom: 1px solid #eee;}
	.initTitle span:nth-child(1){border-left:4px solid #ff2222;padding-left:5px;}
	.initTitle span:nth-child(2){float: right;color: #999;font-size: 12px;}
	
	.hotClassroom{overflow: hidden;}
	.hotClassroom li{float: left;width:150px;padding:0 5px;}
	::-webkit-scrollbar{display:none;}
</style>



