<template>
  <div class="contentHtml">
  	<img class="width100" v-on:click="hrefTo('/circle/circleAdd','创建圈子')" v-bind:src="myCircles.head_img_url"/>
	<ul class="flex iconType">
		<li><p class="colorA">33</p><p>动态</p></li>
		<li><p class="colorA">44</p><p>文字</p></li>
		<li><p class="colorA">2</p><p>课程</p></li>
		<li><p class="colorA">32</p><p>文件</p></li>
	</ul>
	<div class="initBox mb10 color9">圈子公告：三大运营商集体表态移动网大运营商集体表态移动网络流量降费</div>
  	<ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li class="mb10 initBox lineHeight line">
	  			<div class="flex">
		  			<img src="../../assets/chushouwangdian@2x.png" width="40" height="40" style="margin-right: 10px;"/>
		  			<div class="color9">
		  				<div class="flex"><span>会飞的老虎</span><span class="colorA">关注</span></div>
		  				<div class="color3">三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费三大运营商集体表态移动网络流量降费</div>
		  				<div class="flex"  style="margin-right: 10%;">
							<img src="../../assets/img.png" height="85" width="85"/>
							<img src="../../assets/img.png" height="85" width="85"/>
							<img src="../../assets/img.png" height="85" width="85"/>
						</div>
		  				<div class="flex"><span>2018-03-23</span><span class="colorA">删除</span></div>
		  			</div>
	  			</div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
     </van-submit-bar>
     <van-submit-bar  button-text="马上加入"  @submit="onSubmit">
		  <div style="color: #f44;font-size: 16px;margin-left:10px ;">¥ 500</div>
     </van-submit-bar>
     <div class="addcircle" v-on:click="hrefTo('/circle/circleNewsAdd/'+circleId,'发布动态')">发布动态</div>
  </div>
</template>

<script>
$("title").text("转让");
export default {
  name: 'contentHtml',
  data () {
    return {
    	images: [
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg'
    ],
    circleId:this.$route.params.id,
    myCircles:'',
    	listDatas:[],
    	input:'',
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	_this.myCircle();
	if(localStorage.user){
		_this.getList();
	}
  },
  methods: {
  	myCircle:function(){
		var _this = this;
    		var data = {
	        "qzid":_this.$route.params.id,
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/get_qz_by_id.php',data,function(res){
	  		console.log(res)
	  		_this.myCircles = res[0];
			_this.$forceUpdate();
		},'json')
  	},
  	getList:function(){
  		var _this = this;
  		var user = JSON.parse(localStorage.user);
  		$(".lodingDiv").text('').addClass("loding").show();
    		var start = _this.listDatas.length;
    		var per_page = 20;
    		var data = {
    			'qzid':_this.$route.params.id,
			'userid':user.userid,
	        'pageIndex':start,
	        'pageSize':per_page,
	    };
	  	$.get(_this.apiUrl+'/e/wxapi/get_dt_by_id.php',data,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listDatas.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
	hrefTo(val,title){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			var content = {"type":"hrefTo","link":val,"title":title}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		this.$router.push(val);
  		}
	},
	onSubmit:function(){
		
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
Vue.filter('typeFormatter',function (value){
	if(value==1){
		return "类目1";
	}else if(value==2){
		return "类目2";
	}else if(value==3){
		return "类目3";
	}
});

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addcircle{
		position: fixed;bottom: 100px;right: 20px;background: #ff3233; 
		padding: 10px;width: 30px;height: 30px;color: #fff;
		border-radius: 50%;-webkit-border-radius: 50%;text-align: center;
	}
	
	
	.iconType{text-align: center;font-size: 1.3rem;background: #fff;margin-bottom:10px;}
	.iconType li{width:25%;padding:15px;}
	.iconType li img{width:3rem;height: 3rem;}
	.myCircle{margin-top: -30px;color: #fff;line-height: 30px;}
</style>



